 100;\n\n  ul {\n    list-style-type: none;\n    padding: 30px 16px;\n\n    li {\n      padding: 10px 0;\n\n      a {\n        text-decoration: none;\n        font-size: 30px;\n        color: white;\n\n        &:hover {\n          opacity: .7;\n        }\n      }\n    }\n  }\n}\n\n#drawer.open {\n  transform: translate(0, 0);\n}\n\nmain {\n  margin-top: 60px;\n  flex: 1;\n\n  .hero {\n    position: relative;\n    z-index: -1;\n\n    img {\n      width: 100%;\n      max-height: 55vh;\n      object-fit: cover;\n      object-position: 50% 100%;\n      display: block;\n      filter: blur(2px);\n      -webkit-filter: blur(2px);\n    }\n\n    .hero-thumb {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      width: 80%;\n      padding: 20px;\n      font-size: 20px;\n      font-weight: bold;\n      text-align: center;\n      background: $bg-thumb;\n      color: white;\n      border: 3px solid #f1f1f1;\n      transform: translate(-50%, -50%);\n      z-index: 2;\n    }\n  }\n\n  h2 {\n    text-align: center;\n    margin-top: 30px;\n  }\n\n  .posts {\n    display: grid;\n    margin: 20px;\n    padding: 0 30px;\n\n    .card-item {\n      margin: 15px;\n      box-shadow: -4px 4px 8px 2px rgba(0, 0, 0, 0.2);\n      border-radius: 8px;\n      overflow: hidden;\n      \n      .card-head {\n        position: relative;\n        z-index: -1;\n        \n        p {\n          position: absolute;\n          padding: 4px 8px;\n          margin-top: 10px;\n          background: #eaeaea;\n          border-top-right-radius: 4px;\n          border-bottom-right-radius: 4px;\n          box-shadow: 2px 2px 8px 2px rgba(0, 0, 0, 0.2);\n        }\n\n        img {\n          width: 100%;\n        }\n      }\n    }\n\n    .card-desc {\n      padding: 12px;\n\n      .rating {\n        color: #333;\n        font-weight: bold;\n        margin-top: -5px;\n        margin-bottom: 8px;\n      }\n\n      .title {\n        a {\n          color: inherit;\n          text-decoration: none;\n\n          &:hover, &:active {\n            text-decoration: underline;\n            opacity: .7;\n          }\n        }\n      }\n\n      .desc {\n        font-size: .9rem;\n        padding-top: 5px;\n      }\n    }\n  }\n\n  /* detail */\n  .jumbotron-detail {\n    margin-top: 15px;\n    position: relative;\n    z-index: -1;\n    \n    img {\n      width: 100%;\n      max-height: 70vh;\n      object-fit: cover;\n      display: block;\n    }\n\n    .jumbotron-thumb {\n      width: 100%;\n      height: 100%;\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      font-size: 25px;\n      text-align: center;\n      background: $bg-thumb;\n      color: #f1f1f1;\n      text-shadow: 1px 1px 1px black;\n      transform: translate(-50%, -50%);\n      padding-top: 30px\n    }\n  }\n\n  .restaurant-info {\n    padding: 10px 40px;\n\n    section {\n      margin-top: 25px;\n\n      h3 {\n        font-size: 28px;\n        padding-bottom: 5px;\n      }\n    }\n\n    .restaurant-menu {\n      .menu-detail {\n        padding-bottom: 14px;\n        \n        .menu-container {\n          margin-bottom: 35px;\n\n          h4 {\n            padding: 3px 5px;\n            background-color: $base-dark;\n            color: #eaeaea;\n            border-radius: 3px;\n          }\n\n          .menu-list {\n            list-style-type: none;\n\n            .menu-item {\n              padding: 2px 5px;\n              border-bottom: 1px solid $base-dark;\n              margin-bottom: 10px;\n            }\n          }\n        }\n      }\n    }\n\n    .restaurant-review {\n      .review-container {\n        display: grid;\n\n        .review-item {\n          padding: 10px 15px;\n          margin-bottom: 20px;\n          border-top: 5px solid $base-dark;\n          border-right: 5px solid $base-dark;\n          box-shadow: -4px 4px 8px 2px rgba(0, 0, 0, 0.2);\n          border-radius: 8px;\n          overflow: hidden;\n\n          .review-date {\n            margin-top: 4px;\n            font-size: 12px;\n            color: #333;\n          }\n        }  \n      }\n    }\n\n    .review-form {\n      display: grid;\n      grid-template-columns: 1fr;\n      grid-gap: 16px;\n      background: #eaeaea;\n      box-shadow: -4px 4px 8px 2px rgba(0, 0, 0, 0.2);\n      border-radius: 4px;\n      padding: 40px 28px;\n\n      .review-form-group {\n        display: flex;\n        flex-direction: column;\n\n        .review-form label {\n          font-weight: 600;\n          margin-bottom: 8px;\n        }\n\n        .review-form-group input:-webkit-autofill {\n          box-shadow: 0 0 0 100px white inset;\n        }\n\n        .review-form-input-name {\n          font-family: 'Poppins', sans-serif;\n          border: none;\n          border-radius: 4px;\n          font-size: 14px;\n          padding: 6px 10px;\n        }\n\n        .review-form-input-review {\n          font-family: 'Poppins', sans-serif;\n          border: none;\n          border-radius: 4px;\n          font-size: 14px;\n          padding: 6px 10px;\n          resize: none;\n        }\n      }\n\n      .review-form-submit {\n        background-color: $base-dark;\n        border: none;\n        border-radius: 4px;\n        color: #f1f1f1;\n        font-size: 14px;\n        min-height: 44px;\n        transition: transform ease-in-out 0.2s;\n        width: 128px;\n\n        &:hover, &:active {\n          transform: scale(1.1);\n        }\n      }\n    }\n  }\n\n  /* page error */\n  .content-error {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    margin: 0 auto;\n    max-width: 300px;\n    height: 70vh;\n  \n    h1 {\n      font-size: 20px;\n      font-weight: 800;\n      text-align: center;\n    }\n  \n    p {\n      font-size: 16px;\n      text-align: center;\n      font-weight: 300;\n    }\n  }\n}\n\n/* loading */\n.loading-indicator-overlay {\n  background: rgba(0, 0, 0, 0.6);\n  height: 100vh;\n  width: 100vw;\n  position: fixed;\n  top: 0;\n  left: 0;\n}\n\n.loading-indicator {\n  animation: 1s linear infinite spinner;\n  animation-play-state: inherit;\n  border: 10px solid rgba(0, 0, 0, 0.2);\n  border-radius: 50%;\n  border-top: 10px solid #f1f1f1;\n  border-right: 10px solid #f1f1f1;\n  border-bottom: 10px solid #f1f1f1;\n  height: 80px;\n  width: 80px;\n  position: fixed;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n  will-change: transform;\n  z-index: 3;\n}\n\n@keyframes spinner {\n  0% {\n    transform: translate3d(-50%, -50%, 0) rotate(0deg);\n  }\n  100% {\n    transform: translate3d(-50%, -50%, 0) rotate(360deg);\n  }\n}\n\n/* Like */\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\nfooter {\n  padding: 25px;\n  text-align: center;\n  color: #eee;\n  background-color: $base-dark;\n}"],"sourceRoot":""}]);
